@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: hsl(37, 100%, 45%);
  font-family: "Kumbh Sans", sans-serif;
  font-weight: 400;
  font-size: 1.5em;
}

main {
  padding: 3em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

h1 {
  font-size: 1.7em;
  font-weight: 700;
  margin-bottom: 10px;
  margin-top: 0;
}

.span.info {
  margin-bottom: 15px;
}

.card {
  height: 590px;
  width: 450px;
  background-color: hsl(183, 76%, 36%);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.profile-pic {
  padding: 2em;
  height: 250px;
  width: 250px;
  border-radius: 180px;
}

.name {
  margin-right: 10px;
}

.btn {
  margin-top: 10px;
  margin-bottom: 15px;
}

.name,
.skill {
  border-radius: 5px;
  width: 95px;
  height: 45px;
}

.fa-heart {
  border: none;
  background-color: transparent;
  outline: none;
}

.name:hover,
.skill:hover {
  background-color: hsl(355, 70%, 43%);
}

.footer {
  margin-top: 25px;
}

